.my_team__container {
  /* width: 1260px; */
  width: 100%;
  height: 55rem;
  display: grid;
  grid-template-columns: 1fr 8fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  gap: 2rem;
  border-radius: 10px;
  background-color: #1a1a22;
  margin-bottom: 1rem;
}

.team__selection {
  width: 9rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1a22;
  border-radius: 10px 0 0 10px;
  border-right: 1px solid #212330;
  box-sizing: border-box;
  overflow: hidden;
}

.selection__card {
  width: 9rem;
  height: 20%;
  background-color: #22222c;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.hovered {
  background-color: #384859;
}

.swap_container {
  height: 5rem;
  width: 5rem;
  /* background-color: #2e353b; */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #384859;
  transition: all 150ms ease;
}

.plus_symbol {
  height: 3rem;
  width: 3rem;
}

.arrow_symbol {
  height: 3rem;
  width: 3rem;
  position: absolute;
  top: 3rem;
  right: 3.5rem;
  /* color: #778cac; */
  color: #eef5f8;
  padding: 1rem;
  border: 2px solid #eef5f8;
  border-radius: 50%;
  opacity: 0;
  transition: all 250ms ease;
}

.swap_container:hover {
  transform: scale(1.05);
  transform-origin: center;
}

.picture__container {
  width: 9rem;
  height: 100%;
  background-color: #6333ff;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.logo__container {
  position: absolute;
  border-radius: 50%;
  width: 200%;
  height: 200%;
  top: 5%;
  left: -100%;
  /* background-color: #1a1a22; */
}

.logo {
  position: absolute;
  width: 200%;
  height: auto;
  top: 0;
  left: 0;
  transition: transform 250ms ease;
}

.image {
  position: absolute;
  right: -2rem;
  bottom: -4px;
}

.player__picture {
  height: 11rem;
  transition: transform 350ms ease;
}

/*--------------------- right hand side of team layout --------------------- */

.team__data_screen {
  width: 100%;
  height: 55rem;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 1rem 2rem 1rem 0;
  position: relative;
}

.info_title__container {
  height: fit-content;
  width: 100%;
  color: #eef5f8;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  margin-bottom: 1rem;
}

.info_title__container h3 {
  margin: 0;
  padding: 0 2rem 0 0;
  font-size: 2rem;
}

.info_title__container h4 {
  margin: 0;
  font-size: 2rem;
}

.main_info__container {
  height: 48rem;
  width: 100%;
  border-bottom: 1px solid #22222c;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 1rem;
}

.points_comparison_container {
  display: flex;
  gap: 4rem;
  margin-bottom: 4rem;
}

.points_bar_container {
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.my_team_info__container {
  width: 100%;
  height: fit-content;
}

.player_info__container {
  height: 2.5rem;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  border-radius: 10px;
  margin-bottom: 0.5rem;
}

.player_info__container:hover {
  cursor: pointer;
  background-color: #22222c;
}

.player_info__container p:hover {
  color: #6333ff;
}

.info__key {
  margin-bottom: 1rem;
}

.info__key:hover {
  background-color: #1a1a22;
  /* color: #3e4859; */
}

.info__key p:hover {
  color: #9CA5BA;
}

.info__element {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.info__element p {
  margin: 0;
  padding: 0;
}

