.dashboard_container {
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 1rem;
  
}

.main_dash_container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  
}

.left_side {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
}

.lower {
  width: 100%;
  height: 34rem;
  display: flex;
  justify-content: space-between;
  gap: 1rem;
}


.lower_inner {
  width: 50%;
  height: 100%;
  /* background-color: #1e252b; */
  background-color: #1a1a22;
  border-radius: 10px;
  display: flex;
  justify-content: center;
}

.right_side {
  width: 22rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team_list {
  width: 100%;
  height: 54rem;
  background-color: #1a1a22;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
}
