.trending_transfers {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 2rem 0;
    position: relative;
    box-sizing: border-box;
}

.trending_transfers h3 {
    padding: 0 0 0 2rem;
}

.transfer_element {
    height: 2rem;
    width: calc(100% - 2rem);
    display: flex;
    margin: 0.5rem 1rem 0.5rem 1rem;
    padding: 0 1rem;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    box-sizing: border-box;
    border-radius: 10px;
}

.trans_rank {
    width: 25%;
    text-align: left;
}

.trans_name {
    width: 25%;
    text-align: left;
}

.transfer_element:hover {
    background-color: #22222c;
    cursor: pointer;
}

.transfer_element:hover > .trans_name {
    color: #f13a80;
}

.inVal {
    width: 25%;
    text-align: right;
}

.outVal {
    width: 25%;
    text-align: right;
}

.trans_btn {
    width: 3rem;
    height: 2rem;
    position: absolute;
    background-color: transparent;
    color: #778cac;
    border: none;
    border-radius: 10px; 
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
}

.in {
    top: 2rem;
    right: 9.6rem;
}

.out {
    top: 2rem;
    right: 1rem;
}

.trans_btn p:hover {
    color: #f13a80;
}