.points_comparison_container {
  width: 100%;

  padding: 0rem 0 1rem 0;
  box-sizing: border-box;
}

.chart {
  width: calc(50% - 2rem);
  height: 10rem;
}

.points_comparison_graph {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-left: 1px solid white;
  border-bottom: 1px solid white;
}

.two {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-end;
}

.graph_child {
  width: 40%;
  height: 2rem;
  background-color: #212330;
  border: 1px solid #778cac;
}

.gw_points:nth-child(1) {
  width: 77%;
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 3px,
    #465298 3px,
    #465298 6px
  );
}

.gw_points:nth-child(2) {
  width: 56%;
}

.gw_points:nth-child(3) {
  width: 46%;
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 3px,
    #363c61 3px,
    #383e64 6px
  );
}

.graph_child:hover {
  cursor: help;
  color: #ffffff;
  background-color: #34374b;
}

.gw_points:nth-child(4):hover {
  background: linear-gradient(130deg, #7c53ff, #f696ff);
}

.weekly_average {
  width: 2rem;
  height: 20%;
}

.weekly_average:nth-child(odd) {
  height: 50%;
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 3px,
    #363c61 3px,
    #383e64 6px
  );
}

.weekly_average:nth-child(2) {
  height: 30%;
}

.weekly_average:nth-child(3) {
  height: 44%;
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 3px,
    #465298 3px,
    #465298 6px
  );
}

.weekly_average:nth-child(4) {
  height: 79%;
}

.weekly_average:nth-child(5) {
  height: 62%;
}

.weekly_average:nth-child(1) {
  height: 59%;
}

.weekly_average:nth-child(6) {
  height: 59%;
}

.weekly_average:nth-child(7) {
  height: 39%;
  background: repeating-linear-gradient(
    45deg,
    transparent,
    transparent 3px,
    #465298 3px,
    #465298 6px
  );
}

.weekly_average:hover {
  cursor: help;
  color: #ffffff;
  background-color: #34374b;
}

.weekly_average:nth-child(7):hover {
  background: linear-gradient(130deg, #7c53ff, #f696ff);
}
