.points_and_ranks {
  width: 100%;
}

.points_and_ranks_inner {
  /* width: 270px; */
  width: 100%;
  height: 30rem;
  /* background-color: #262d34; */
  background-color: #1a1a22;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  box-sizing: border-box;
  padding: 2rem;
}

.points_and_ranks h3 {
  margin-bottom: 1rem;
  padding: 0;
}

.points_and_ranks h4 {
  margin: 0;
  font-size: 15px;
  color: #eef5f8;
}

.points_and_ranks p {
  font-size: 14px;
}

.pr {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}

.gameweek_link {
  position: absolute;
  bottom: 1rem;
  right: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.gameweek_link p {
  margin-right: 0.5rem;
}

.gameweek_link svg {
  height: 12px;
  color: #f13a80;
  padding-top: 5px;
}

.gameweek_link:hover {
    color: #F13A80;
    border-bottom: 1px solid #F13A80;
}
