.points_table_one {
  margin: 0 0 1rem 0;
}

.points_title_bar {
  width: 90%;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  box-sizing: border-box;
  text-align: center;
}
.player_pts_title {
  width: 60%;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  border-bottom: 1px solid #3e4859;
  padding-bottom: 1rem;
}

.team_pts_title {
  width: 40%;
  box-sizing: border-box;
  border-bottom: 1px solid #778cac;
  padding-bottom: 1rem;
}

.points_table_container {
  display: flex;
  align-items: flex-end;
  padding: 0;
  box-sizing: border-box;
}

table.points_table {
  width: 90%;
  text-align: center;
  border-collapse: collapse;
  box-sizing: border-box;
}
table.points_table td,
table.points_table th {
  padding: 1rem 0.5rem;
  /* border-right: 1px solid #3e4859; */
  border-bottom: 1px solid #3e4859;
}
table.points_table tbody td {
  font-size: 16px;
  font-weight: 600;
}
table.points_table tr:nth-child(3) {
  background: #22222c;
}
table.points_table tfoot td {
  font-size: 16px;
}

.points_table svg {
  width: 1rem;
  height: 1rem;
  fill: rgba(240, 248, 255, 0.596);
}

.solo_kill {
  color: #c48e2b73;
}

.total_container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 7rem;
  height: 100%;
  background-color: #22222c;
  box-sizing: border-box;
  border: 1px solid #3e4859;
}
