.points_bar__container {
  width: 100%;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 1.5rem;
  box-sizing: border-box;
  }

.points__bar {
  width: 100%;
  height: 3rem;
  display: flex;
  flex-direction: row;
  text-align: center;
}

.points_bar__container h4 {
    margin: 0.5rem 0 0 0;
}

.pb_child {
  height: 100%;
  background-color: #212330;
  border: 1px solid #778cac;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pb_child:hover {
    cursor: pointer;
    background: linear-gradient(130deg, #34374b, #34374b);
    color: #ffffff;
}

