.player {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr) 4fr;
  grid-template-rows: 1fr;
  grid-column-gap: px;
  grid-row-gap: 0px;
  height: 4rem;
  align-items: center;
  width: 95%;
  margin-bottom: 0.5rem;
  border-radius: 10px;
}

.player:hover {
  cursor: pointer;
  background-color: #22222c;
}

.player:hover > button {

  background-color: #34374b;
}

.img_container {
  width: 3rem;
  height: 3rem;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  border-radius: 50%;
  background-color: #6333ff;
  position: relative;
  margin-right: 0.7rem;
}

/* .player img {
  height: 5rem;
  position: absolute;
  bottom: -1rem;
} */

.player button {
  /* background-color: #6333ff; */
  background-color: #212330;
  border-radius: 10px 0 0 10px;
  border: none;
  height: 4rem;
  width: 8rem;
  color: #eef5f8;
  letter-spacing: 0.5px;
  font-weight: 500;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 150ms ease-out;
}

.logo__container_sm {
  border-radius: 50%;
}

.picture__container {
  width: 100%;
  height: 100%;
  background-color: #6333ff;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.logo_sm {
  position: absolute;
  width: auto;
  height: 4.5rem;
  top: 1rem;
  right: -2rem;
  transition: transform 250ms ease;
}

.image_sm {
  position: absolute;
 left: -1rem;
  bottom: -1rem;
}

.player__image_sm {
  height: 4rem;
}

.player button:hover {
  background-color: #6333ff;
  cursor: pointer;
  color: #f8f8f8;
}

.player_name {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

#name_text_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.player_name h4 {
  margin: 0;
  padding: 0;
  font-size: larger;
  color: #eef5f8;
  /* color: #0ebf6f; */
}

.player_name p {
  margin: 0;
  padding: 0;
  font-size: 12px;
  letter-spacing: 0.5px;
}

.player_stats {
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

.player_stats p {
  margin: 0;
  padding: 0;
  font-size: 12px;
  letter-spacing: 0.5px;
}

.stats {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0 10px 0;
  box-sizing: border-box;
}

.stats h4 {
  margin: 0;
  color: #778cac;
}
