nav {
    width: 100%;
    height: 8rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    z-index: 900;
}

ul {
    width: 15rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}

li {
    list-style: none;
    color: #5f646b;
    font-weight: 600;
}

li:hover {
    cursor: pointer;
    color: #F13A80;
}

.log_sign_container {
    position: absolute;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;
}

#sign_up {
    background-color: #F13A80;
    padding: 1rem 2rem;
    border-radius: 10px;
    color: white;
}

a {
    text-decoration: none;
    color: #5f646b;
}

a:hover {
    cursor: pointer;
    color: #F13A80;
}

