.league_section {
  background-color: #1a1a22;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  padding-bottom: 2rem;
}

div.main_leaderboard {
  background-color: #1a1a22;
  width: 100%;
  height: 100%;
  text-align: center;
}
.divTable.main_leaderboard .divTableCell,
.divTable.main_leaderboard .divTableHead {
  padding: 1.5rem 2rem;
}
.divTable.main_leaderboard .divTableBody .divTableCell {
  font-size: 15px;
  font-weight: bold;
  color: #778cac;
}
.divTable.main_leaderboard .divTableHeading {
  /* background: #22222c; */
  background-color: transparent;
  border-radius: 10px 0 0 10px;
  height: 6rem;
  border-collapse: separate;
  overflow: hidden;
}
.divTableHeadingCover {
  height: 4rem;
  width: 100%;
  background-color: #22222c4d;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}
.divTable.main_leaderboard .divTableHeading .divTableHead {
  font-size: 15px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
}
.main_leaderboard .tableFootStyle {
  font-size: 15px;
}
.main_leaderboard .tableFootStyle .links {
  text-align: right;
}
.main_leaderboard .tableFootStyle .links a {
  display: inline-block;
  background: #ffffff;
  color: #398aa4;
  padding: 2px 8px;
  border-radius: 10px;
}
.main_leaderboard.outerTableFooter {
  border-top: none;
}
.main_leaderboard.outerTableFooter .tableFootStyle {
  padding: 3px 5px;
}
.divTable {
  display: table;
  width: 100%;
}
.divTableRow {
  display: table-row;
  width: 100%;
}
.divTableRow:hover {
  cursor: pointer;
  background-color: #22222c;
}
.divTableCell,
.divTableHead {
  display: table-cell;
}
.divTableHeading {
  display: table-header-group;
}
.divTableHeading:hover {
  background-color: none;
}
.divTableFoot {
  display: table-footer-group;
}
.divTableBody {
  display: table-row-group;
}

/* ------------- pagination container ------------------- */

.pagination_container {
  display: flex;
  list-style-type: none;
  height: 2rem;
  width: 100%;
  /* background-color: #398aa4; */
  justify-content: center;
  margin-top: 2rem;
}

.pagination_item {
  height: 2.2rem;
  width: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

.pagination_item:hover {
  color: #778cac;
  cursor: pointer;
}

/* ------- right side -------------- */

.league_rules {
  width: 100%;
  height: 30rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #1a1a22;
  border-radius: 10px;
  padding: 2rem;
  box-sizing: border-box;
}

.rules_title {
  height: 2rem;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.rules_title h4 {
  margin: 0 0 1rem 0;
}

.rules_list {
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 100%;
  gap: 0.7rem;
}

.rules_list > li {
  list-style: circle;
  color: #9CA5BA;
}

.rules_list > li:hover {
  color: #9CA5BA;
}
