h3 {
  margin: 0 0 2rem 0;
  /* color: #f13a80; */
  padding: 0 2rem;
}

.team_list_inner h4 {
  /* color: #f13a80; */
  padding: 0 2rem 3px 2rem;
}



.team_list_inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  box-sizing: border-box;
  margin-top: 2rem;
}

.chevron_right {
  color: #f13a8077;
}
.chevron_up {
  color: green;
}
.chevron_down {
  color: red;
}

.own_position {
  height: 5rem;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 1rem;
}

.own_position h4 {
  padding: 0 2rem;
  margin: 0 0 1rem 0;
}

.table {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.user_position {
  height:3rem;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin-bottom: 0.5rem;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 0 1rem;
  margin-right: 1rem;
  margin-left: 1rem;
}

.user_position:hover {
  cursor: pointer;
  background-color: #22222c;
}

.user_position:hover > .more_info > .chevron_right {
  color: #f13a80;
}

.rank_div {
  display: flex;
  align-items: center;
}

.user_p_children {
  height: 3rem;
  width: 2.3rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.more_info {
  height: 3rem;
  width: 3rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: 15px;
  padding-top: 1px;
}
