.upcoming {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 2rem;
  box-sizing: border-box;
  position: relative;
  background-color: #1a1a22;
  border-radius: 10px;
}

.upcoming h3 {
  padding: 0;
  margin: 0 0 0.5rem 0;
}

.btn_container {
  position: absolute;
  top: 1.5rem;
  right: 2rem;
  height: 3rem;
  width: 10.5rem;
  overflow: hidden;

  display: flex;
  justify-content: center;
  align-items: center;
}

.upcoming_btn {
  width: 10rem;
  height: 2.5rem;
  padding: 0 1rem;
  background-color: #0ebf6f;
  border-radius: 5px;
  border: 1px solid #0ebf6f;
  /* color: #eef5f8; */
  color: #1a1a22;
  font-size: 16px;
  font-weight: 600;
  font-family: Arial, Helvetica, sans-serif;
  transition: all 250ms cubic-bezier(0.74, 0.16, 0.24, 0.92);
  cursor: pointer;
}

.button_marker {
  position: relative;
  width: fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 250ms cubic-bezier(0.74, 0.16, 0.24, 0.92);
  overflow: hidden;
  border-radius: 5px;
}

.rippleSpan {
  position: absolute;
  /* top: -5rem;
  left: -5rem; */
  width: 8rem;
  height: 6rem;
  background: rgba(255, 255, 255, 0.425);
  pointer-events: none;
  border-radius: 50%;
  animation: animate 700ms linear infinite;
}

.button_marker:hover {
  scale: 1.03;
}

.upcoming_btn:active {
  scale: 0.96;
}

@keyframes animate {
  0% {
    scale: 0.1;
  }
  100% {
    scale: 2;
  }
}

.schedule {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.schedule_element {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin-bottom: 0.5rem;
}

.side {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.side_bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: radial-gradient(
    circle,
    rgba(255, 255, 255, 0.2) 0%,
    rgba(255, 255, 255, 0.158263989189426) 14%,
    rgba(255, 255, 255, 0) 78%
  );
  opacity: 0;
  transition: all 350ms ease-in;
}

.side > .side_bg:hover {
  opacity: 0.2;
  cursor: pointer;
}

.vs {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

img {
  height: 50px;
  cursor: pointer;
}

.schedule p {
  padding: 0.5rem 0;
}

.day {
  margin-bottom: 0.5rem;
}
