.top_bar {
  width: fit-content;
  /* background-color: blue; */
  display: flex;
  margin-top: 1rem;
  margin-left: 0.3rem;
}

.top_bar button {
  width: 3rem;
  height: 3rem;
  border-radius: 1.5rem;
  border: none;
  margin: 0 0.5rem;
  background-color: #151a1e85;
}

.top_bar button:hover {
  cursor: pointer;
  background-color: #434552;
}

#top {
  background-image: url("./Top_icon.webp");
  background-size: 82%;
  background-position: 0.25rem 0.3rem;
  background-repeat: no-repeat;
}

#jgl {
  background-image: url("./Jungle_icon.webp");
  background-size: 82%;
  background-position: 0.25rem 0.3rem;
  background-repeat: no-repeat;
}

