#searchbar_container {
  position: relative;
}

#searchbar {
  width: 3rem;
  height: 3rem;
  border-radius: 1.5rem;
  border: none;
  margin: 0 0.5rem;
  padding: 1px 4px;
  box-sizing: border-box;
  background-color: #151a1e85;
  transition: 100ms linear all;
  color: transparent;
}

.magnifying_glass {
  position: absolute;
  left: 1.5rem;
  top: 1rem;
  cursor: pointer;
}

#searchbar:focus {
  width: 10rem;
  padding-left: 3rem;
  color: aliceblue;
}

.player_list {
  width: 100%;
  height: 57rem;
  background-color: #1a1a22;
  border-radius: 10px;
}

.players_container {
  width: 100%;
  height: 100%;
  padding: 1.5rem 2rem 1rem 2rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}


