.close_player_info {
  width: 3rem;
  height: 3rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  background-color: #34374b;
  border-radius: 50%;
  border: none;
  transition: all 250ms ease-in;
  transform-origin: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.close_player_info:hover {
  cursor: pointer;
  background-color: #778cac;
  border-radius: 10px;
}

.close_symbol {
  height: 1.4rem;
  width: 1.4rem;
  color: #778cac;
  transition: all 100ms ease-in-out;
}

.close_player_info:hover > .close_symbol {
  color: #34374b;
  scale: 1.3;
}


