.selection_panel {
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: flex-start;
  position: relative;
  padding: 1rem 0;
  border-bottom: 1px solid #363b4175;
  margin-bottom: 3rem;
}

.dash_nav {
  width: 250px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}

span {
  padding-bottom: 1.6rem;
}

li > .active {
  border-bottom: 5px solid #f13a80;
}
